<template>
  <div id="school__apply">
    <common-header title="我要申请"></common-header>

    <Cell title="学校名称" type="input" placeholder="学校名称" v-model="schoolName"/>
    <Cell title="姓名" type="input" placeholder="申请人姓名" v-model="userName"/>
    <Cell title="联系电话" type="tel" placeholder="联系电话" v-model="mobile" />
    <Cell title="队员姓名" type="input" placeholder="可不填，队员以逗号分隔" v-model="teamer"/>

    <div class="apply__text">
      <div class="apply__text__title title">创业说明</div>
      <my-text-area :maxlength="100" placeholder="输入您的创业说明" @change="getValue"></my-text-area>
    </div>
    <div class="apply__uploadImg border">
      <div class="apply__uploadImg__title title">照片</div>
      <UploadImg @getIds="getImgs" :maxImgNum='1'/>
    </div>

    <div class="apply__identity border">
      <div class="apply__identity__title title">身份认证</div>
      <div class="apply__identity__upload">
        <div class="upload">
          <UploadImgs @getIds="getIdImg" :maxImgNum='1'/>
          <div class="upload__text">身份证正面照</div>
        </div>
        <div class="upload">
          <UploadImgt @getIds="getStuImg" :maxImgNum='1'/>
          <div class="upload__text">学生证</div>
        </div>
      </div>
    </div>

    <div class="apply__tips">
      <p class="apply__tips__text">温馨提示</p>
      <p class="apply__tips__text">1.为了保证您的认证顺利，请保证信息清晰可见。</p>
      <p class="apply__tips__text">2.您提交的信息仅用于申请创业基金认证，请放心上传。</p>
    </div>

    <div class="apply__submit" :class="{active:isActive}" @click="JoinApplySubmit">
      提交申请
    </div>
  </div>
</template>

<script>
  import Cell from '@/components/Cell/Cell';
  import MyTextArea from '@/components/TextArea/TextArea';
  import UploadImg from '@/components/UploadImg/UploadImg'
  import UploadImgs from '@/components/UploadImgs/UploadImgs'
  import UploadImgt from '@/components/UploadImgt/UploadImgt'

  export default {
    data() {
      return {
        imgList: [],//图片
        imgIdList:[],//身份证
        imgStuList:[],//学生证
        schoolName:'',
        userName:'',
        mobile:'',
        teamer:'',
        textArea:'',
      }
    },
		computed:{
			isActive(){
				if(this.schoolName !='' && this.userName !='' && this.mobile !='' && this.imgList.length>0 && this.imgIdList.length>0 && this.imgStuList.length > 0 && this.textArea.length > 0){
					return true
				}else{
					return false
				}
			}
		},
    methods: {
    	getValue(val){
    		this.textArea = val
    	},
      getImgs(ids){
    		console.log(ids)
    		this.imgList = ids
    	},
      getIdImg(ids){
    		console.log(ids)
    		this.imgIdList = ids
    	},
      getStuImg(ids){
    		console.log(ids)
    		this.imgStuList = ids
    	},
    	JoinApplySubmit(){
    		let params = {
    			action : 1,
					submiterName:this.userName,
					memberNames:this.teamer,
					activityOrgName:this.schoolName,
					applyDesc:this.textArea,
					tel1:this.mobile,
					imageIds:this.imgList.length > 0 ? this.imgList.join(','):'',
					idCardImageIds:this.imgIdList.length > 0 ? this.imgIdList.join(','):'',
					schoolCardImageIds:this.imgStuList.length > 0 ? this.imgStuList.join(','):''
    		}
    		console.log(params)
    		if(this.isActive){
    			this.$Api.JoinApplySubmit(params).then((res)=>{
    				console.log(res)
    				if(res.q.s == 0){
							this.$toast('申请成功')  					
    				}else{
   						this.$toast(res.q.d)  					
    				}
    			})
    		}else{
    			this.$toast('请输入完整信息')
    		}
    	},
    },

    components: {
      Cell,
      MyTextArea,
      UploadImg,
      UploadImgs,
      UploadImgt
    },
  }
</script>

<style lang="scss">
  #school__apply {
    padding-bottom: 2rem;

    .apply__text {
      border-top: 1px solid #eee;
      padding-bottom: .2rem;
    }

    .title {
      padding: .4rem 0 .3rem 0;
      padding-left: .4rem;
      font-size: 0.427rem;
    }

    #UploadImg {
      padding-top: 0;
      padding-bottom: 0;
    }

    .apply__identity {
      #UploadImg {
        display: inline-block;
        
        ul > li {
          margin-bottom: 0;
        }
      }

      .apply__identity__upload {
        display: flex;

        .upload {
          width: 50%;

          .upload__text {
            padding: .1rem 0;
            padding-left: .4rem;
            font-size: 0.37rem;
            color: #666666;
          }
        }
      }
    }

    .apply__tips {
      padding: .4rem 0 0 .4rem;
      line-height: .53rem;
      font-size: 0.37rem;
      color: #666666;
    }

    .apply__submit {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 10rem;
      height: 1.31rem;
      font-size: 0.45rem;
      line-height: 1.31rem;
      color: #fff;
      text-align: center;
      background: #cccccc;

      &.active {
        background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
      }
    }

    .border {
      border-top: 1px solid #eee;
    }
  }
</style>



